import React, {Component} from "react";
import {GrMail} from "react-icons/gr";
import "./Contact.css";

export default class Contact extends Component {
	constructor() {
		super();

		this.state = {
			selected: -1,
		};
	}

	render() {
		return (
			<div className="fade-in contact-container">
				<div className="info">
					{/* <GrMail size="20vw"></GrMail> */}
					<h1 className="header">Contact</h1>
					<div className="paragraph muted break-word">
						Please, fill in the form to the right in order to contact me. Your message will be sent to me via e-mail, I will contact you manually from there.
					</div>
				</div>
				<div className="form">
					<div className="personal-info">
						<input type="text" className="name" placeholder="Name" />
						<input type="text" className="surname" placeholder="Surname" />
					</div>
					<div className="footer">
						<button className="submit-button" type="submit">
							Submit!
						</button>
					</div>
				</div>
			</div>
		);
	}
}
